import React from "react"

import Layout from "../components/layout"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import SEO from "../components/seo"

export default ({ data }) => (
  <Layout>
    <SEO title="About" />
    <h1>About</h1>
    <p>&emsp;大學期間主修物理學，碩士在台灣大學就讀電子所。期間與台積電產學中心合作 研究半
      導體相關領域，並以第一作者在AIP Advances發表相關著作。擅長以程式作為一種語言，並以人
      工智慧和機械動力裝置作為創作的媒材，嘗試探索在藝術中人與機器協力合作的更多可能性。
    </p>
    <p>&emsp;此外，素描創作《最美的數學公式》曾被受邀並刊登在562期的科學月刊。在未來希望能
      運用所學，做到將科學精神、現代科技等元素融入藝術、我們的社會、我們的生活。
    </p>
    <Img fluid={data.file.childImageSharp.fluid} />
  </Layout>
)

export const query = graphql`
  query {
    file(relativePath: {eq: "about.jpg"}) {
      childImageSharp {
        fluid(maxWidth: 500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`